import styles from './page.module.css';
import Image from 'next/image'; 

export default function Page() {
  return (
    <main>
       
        <header className={styles.mainHeader}>
            <div className={styles.headerContent}>
                <h1>Ligas Recreativas UACH</h1>
                <p>Descubre, participa y vive la pasión por el deporte en las Ligas Recreativas de la Universidad Austral de Chile.</p>
            </div>
        </header>

    
        <nav className={styles.sportNavigation}>
            <button className={styles.navButton}>Fútbol</button>
            <button className={styles.navButton}>Vóleibol</button>
            <button className={styles.navButton}>Básquetbol M</button>
            <button className={styles.navButton}>Básquetbol F</button>
        </nav>

        <section className={`${styles.sportSection} ${styles.sportFutbol}`}>
            <div className={styles.pointsContainer}>
                <div className={styles.point}></div> 
                <div className={styles.point}></div>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
            </div>
            <div className={styles.courtContainer}>
                <Image 
                    src="/images/cancha_de_futbol.jpg" 
                    alt="Cancha de Fútbol" 
                    width={400} // ANCHO
                    height={180} // ALTO
                    className={styles.courtImage} 
                />
                <a href="#" className={`${styles.matchesButton} ${styles.btnFutbol}`}>Partidos</a>
            </div>
            <div className={styles.pointsContainer}>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
            </div>
        </section>

        <section className={`${styles.sportSection} ${styles.sportVoleibol}`}>
            <div className={styles.pointsContainer}>
                <div className={styles.point}></div> 
                <div className={styles.point}></div>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
            </div>
            <div className={styles.courtContainer}>
                <Image 
                    src="/images/cancha_de_voley.jpg" 
                    alt="Cancha de Vóleibol" 
                    width={400} 
                    height={180} 
                    className={styles.courtImage} 
                />
                <a href="#" className={`${styles.matchesButton} ${styles.btnVoleibol}`}>Partidos</a>
            </div>
            <div className={styles.pointsContainer}>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
            </div>
        </section>

        <section className={`${styles.sportSection} ${styles.sportBasquetbol}`}>
            <div className={styles.pointsContainer}>
                <div className={styles.point}></div> 
                <div className={styles.point}></div>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
            </div>
            <div className={styles.courtContainer}>
                <Image 
                    src="/images/cancha_de_basquetbol.webp" 
                    alt="Cancha de Básquetbol" 
                    width={400} 
                    height={180} 
                    className={styles.courtImage} 
                />
                <a href="#" className={`${styles.matchesButton} ${styles.btnBasquetbol}`}>Partidos</a>
            </div>
            <div className={styles.pointsContainer}>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
                <div className={styles.point}></div>
            </div>
        </section>
    </main>
  );
}